.pagination {
  margin: 0;
  display: inline-flex;
  padding: 0;
  list-style: none;
  outline: none;
  justify-content: center;
  align-items: center;
}
.wrapper {
  > img {
    display: inline;
    cursor: pointer;
    top: 2px;
  }
  display: flex;
  justify-content: center;
  align-items: center;
}
.pagination > .active > a {
  /* background-color: #47ccde ; */
  background-color: var(--primary-color);
  /* border-color: #47ccde ; */
  color: #fff;
}
/* .pagination > li{
    display: flex;
    justify-content: center;
    align-items: center;
  } */
.pagination > li > a {
  /* border: 1px solid #47ccde ; */
  border-radius: 100%;
  padding: 0px 5px;
  font-size: 16px;
  margin: 0px 2px;
  outline: none;
  cursor: pointer;
}

.pagination > li > a > img {
  position: relative;
  top: 2px;
}
.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  /* background-color: #47ccde ; */
  background-color: var(--primary-color);
  border-color: #47ccde;
  outline: none;
}
.pagination > li > a,
.pagination > li > span {
  /* color: #47ccde */
  color: #000000;
}
.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  border-radius: unset;
}
